import React from 'react';
import Iframe from 'react-iframe'


class Projets extends React.Component{

    render(){
        return (
              <section id="projets">
                  <div className="video">
                    <h3>Streaming interface</h3>
                    <Iframe src="https://streamable.com/e/5au6h5" frameborder="0" width="100%" height="100%" allowFullScreen />
                  </div>
                  <div className="video">
                      <h3>Copie Interface Diney+</h3>
                      <Iframe src="https://streamable.com/e/nz3qq8" frameborder="0" width="100%" height="100%" allowFullScreen />
                  </div>
                  <div className="video">
                      <h3>App Team Management basic</h3>
                      <Iframe src="https://streamable.com/e/h8ec2m" frameborder="0" width="100%" height="100%" allowFullScreen />
                  </div>
              </section> 
        )
    }
}

export default Projets;