import React from 'react';


class About extends React.Component{

    render(){
        return (
                <section id="about">
                  <h3>À propos</h3>
                  <div className="underline"></div>
                  <div className="text">
                    <div className="barre2"></div>
                    <p>De nature rigoureuse et persévérante, j’ai décidé d’entreprendre, il y a un an, une reconversion dans le secteur du numérique afin de pouvoir exercer dans le domaine qui me passionne, j’en suis maintenant à consolider mes acquis, ce que je souhaite assurer par le biais d’une alternance.<br/>
                    Passionnée par l'univers du jeu vidéo et de l'informatique, je souhaite peaufiner mes connaissances dans le domaine du développement afin de m'orienter si possible sur le développement de jeux vidéos et de réalités virtuelles.</p>
                  </div>
                </section> 
        )
    }
}

export default About;